	module.exports = {"data":{"postsJson":{"title":"Sign Six","questionsTitle":"OPENING THE WORD","display":"block","bookName":"John - ΚΑΤΑ ΙΩΑΝΝΗΝ","ref":"9:1-10:42","html":"<p>   <strong>9</strong><u>1</u>Καὶ παράγων εἶδεν ἄνθρωπον τυφλὸν ἐκ γενετῆς. <u>2</u>καὶ ἠρώτησαν αὐτὸν οἱ μαθηταὶ αὐτοῦ λέγοντες·</p><h1>THE DISCIPLES </h1><h3>ῥαββί, τίς ἥμαρτεν, οὗτος ἢ οἱ γονεῖς αὐτοῦ, ἵνα τυφλὸς γεννηθῇ;</h3><p><u>3</u>ἀπεκρίθη Ἰησοῦς·</p><h1>JESUS </h1><h2>οὔτε οὗτος ἥμαρτεν οὔτε οἱ γονεῖς αὐτοῦ, ἀλλʼ ἵνα φανερωθῇ τὰ ἔργα τοῦ θεοῦ ἐν αὐτῷ. <u>4</u>ἡμᾶς δεῖ ἐργάζεσθαι τὰ ἔργα τοῦ πέμψαντός με ἕως ἡμέρα ἐστίν· ἔρχεται νὺξ ὅτε οὐδεὶς δύναται ἐργάζεσθαι. <u>5</u>ὅταν ἐν τῷ κόσμῳ ὦ, φῶς εἰμι τοῦ κόσμου.</h2><p><u>6</u>Ταῦτα εἰπὼν ἔπτυσεν χαμαὶ καὶ ἐποίησεν πηλὸν ἐκ τοῦ πτύσματος καὶ ἐπέχρισεν αὐτοῦ τὸν πηλὸν ἐπὶ τοὺς ὀφθαλμοὺς <u>7</u>καὶ εἶπεν αὐτῷ·</p><h1>JESUS </h1><h2>ὕπαγε νίψαι εἰς τὴν κολυμβήθραν τοῦ Σιλωάμ</h2><p>ὃ ἑρμηνεύεται ἀπεσταλμένος). ἀπῆλθεν οὖν καὶ ἐνίψατο καὶ ἦλθεν βλέπων.<br />   <u>8</u>Οἱ οὖν γείτονες καὶ οἱ θεωροῦντες αὐτὸν τὸ πρότερον ὅτι προσαίτης ἦν ἔλεγον·<br />   A Blind Beggar’s Friends </p><h4>οὐχ οὗτός ἐστιν ὁ καθήμενος καὶ προσαιτῶν;</h4><p><u>9</u>ἄλλοι ἔλεγον ὅτι<br />   A Blind Beggar’s Friends </p><h4>οὗτός ἐστιν,</h4><p>ἄλλοι ἔλεγον·</p><h1>A BLIND BEGGAR’S FRIENDS </h1><h4>οὐχί, ἀλλʼ ὅμοιος αὐτῷ ἐστιν.</h4><p>ἐκεῖνος ἔλεγεν ὅτι</p><h1>A BLIND BEGGAR </h1><h4>ἐγώ εἰμι.</h4><p><u>10</u>ἔλεγον οὖν αὐτῷ·</p><h1>A BLIND BEGGAR’S FRIENDS </h1><h4>πῶς οὖν ἠνεῴχθησάν σου οἱ ὀφθαλμοί;</h4><p><u>11</u>ἀπεκρίθη ἐκεῖνος ·</p><h1>A BLIND BEGGAR </h1><h4>ὁ ἄνθρωπος ὁ λεγόμενος Ἰησοῦς πηλὸν ἐποίησεν καὶ ἐπέχρισέν μου τοὺς ὀφθαλμοὺς καὶ εἶπέν μοι ὅτι ὕπαγε εἰς τὸν Σιλωὰμ καὶ νίψαι· ἀπελθὼν οὖν καὶ νιψάμενος ἀνέβλεψα.</h4><p><u>12</u>καὶ εἶπαν αὐτῷ·</p><h1>A BLIND BEGGAR’S FRIENDS </h1><h4>ποῦ ἐστιν ἐκεῖνος;</h4><p>λέγει·</p><h1>A BLIND BEGGAR </h1><h4>οὐκ οἶδα.</h4><p>   <u>13</u>Ἄγουσιν αὐτὸν πρὸς τοὺς Φαρισαίους τόν ποτε τυφλόν. <u>14</u>ἦν δὲ σάββατον ἐν ᾗ ἡμέρᾳ τὸν πηλὸν ἐποίησεν ὁ Ἰησοῦς καὶ ἀνέῳξεν αὐτοῦ τοὺς ὀφθαλμούς. <u>15</u>πάλιν οὖν ἠρώτων αὐτὸν καὶ οἱ Φαρισαῖοι πῶς ἀνέβλεψεν. ὁ δὲ εἶπεν αὐτοῖς·</p><h1>A BLIND BEGGAR </h1><h4>πηλὸν ἐπέθηκέν μου ἐπὶ τοὺς ὀφθαλμοὺς καὶ ἐνιψάμην καὶ βλέπω.</h4><p><u>16</u>ἔλεγον οὖν ἐκ τῶν Φαρισαίων τινές·</p><h1>THE PHARISEES </h1><h4>οὐκ ἔστιν οὗτος παρὰ θεοῦ ὁ ἄνθρωπος, ὅτι τὸ σάββατον οὐ τηρεῖ.</h4><p>ἄλλοι δὲ ἔλεγον·</p><h1>THE PHARISEES </h1><h4>πῶς δύναται ἄνθρωπος ἁμαρτωλὸς τοιαῦτα σημεῖα ποιεῖν;</h4><p>καὶ σχίσμα ἦν ἐν αὐτοῖς. <u>17</u>λέγουσιν οὖν τῷ τυφλῷ πάλιν·</p><h1>THE PHARISEES </h1><h4>τί σὺ λέγεις περὶ αὐτοῦ, ὅτι ἠνέῳξέν σου τοὺς ὀφθαλμούς;</h4><p>ὁ δὲ εἶπεν ὅτι</p><h1>A BLIND BEGGAR </h1><h4>προφήτης ἐστίν.</h4><p>   <u>18</u>Οὐκ ἐπίστευσαν οὖν οἱ Ἰουδαῖοι περὶ αὐτοῦ ὅτι ἦν τυφλὸς καὶ ἀνέβλεψεν ἕως ὅτου ἐφώνησαν τοὺς γονεῖς αὐτοῦ τοῦ ἀναβλέψαντος <u>19</u>καὶ ἠρώτησαν αὐτοὺς λέγοντες·</p><h1>JEWISH LEADERS </h1><h4>οὗτός ἐστιν ὁ υἱὸς ὑμῶν, ὃν ὑμεῖς λέγετε ὅτι τυφλὸς ἐγεννήθη; πῶς οὖν βλέπει ἄρτι;</h4><p><u>20</u>ἀπεκρίθησαν οὖν οἱ γονεῖς αὐτοῦ καὶ εἶπαν·</p><h1>A BLIND BEGGAR’S PARENTS </h1><h4>οἴδαμεν ὅτι οὗτός ἐστιν ὁ υἱὸς ἡμῶν καὶ ὅτι τυφλὸς ἐγεννήθη· <u>21</u>πῶς δὲ νῦν βλέπει οὐκ οἴδαμεν, ἢ τίς ἤνοιξεν αὐτοῦ τοὺς ὀφθαλμοὺς ἡμεῖς οὐκ οἴδαμεν· αὐτὸν ἐρωτήσατε, ἡλικίαν ἔχει, αὐτὸς περὶ ἑαυτοῦ λαλήσει.</h4><p><u>22</u>ταῦτα εἶπαν οἱ γονεῖς αὐτοῦ ὅτι ἐφοβοῦντο τοὺς Ἰουδαίους· ἤδη γὰρ συνετέθειντο οἱ Ἰουδαῖοι ἵνα ἐάν τις αὐτὸν ὁμολογήσῃ χριστόν, ἀποσυνάγωγος γένηται. <u>23</u>διὰ τοῦτο οἱ γονεῖς αὐτοῦ εἶπαν ὅτι ἡλικίαν ἔχει, αὐτὸν ἐπερωτήσατε.<br />   <u>24</u>Ἐφώνησαν οὖν τὸν ἄνθρωπον ἐκ δευτέρου ὃς ἦν τυφλὸς καὶ εἶπαν αὐτῷ·</p><h1>JEWISH LEADERS </h1><h4>δὸς δόξαν τῷ θεῷ· ἡμεῖς οἴδαμεν ὅτι οὗτος ὁ ἄνθρωπος ἁμαρτωλός ἐστιν.</h4><p><u>25</u>ἀπεκρίθη οὖν ἐκεῖνος·</p><h1>A BLIND BEGGAR </h1><h4>εἰ ἁμαρτωλός ἐστιν οὐκ οἶδα· ἓν οἶδα ὅτι τυφλὸς ὢν ἄρτι βλέπω.</h4><p><u>26</u>εἶπον οὖν αὐτῷ·</p><h1>JEWISH LEADERS </h1><h4>τί ἐποίησέν σοι; πῶς ἤνοιξέν σου τοὺς ὀφθαλμούς;</h4><p><u>27</u>ἀπεκρίθη αὐτοῖς·</p><h1>A BLIND BEGGAR </h1><h4>εἶπον ὑμῖν ἤδη καὶ οὐκ ἠκούσατε· τί πάλιν θέλετε ἀκούειν; μὴ καὶ ὑμεῖς θέλετε αὐτοῦ μαθηταὶ γενέσθαι;</h4><p><u>28</u>καὶ ἐλοιδόρησαν αὐτὸν καὶ εἶπον·</p><h1>JEWISH LEADERS </h1><h4>σὺ μαθητὴς εἶ ἐκείνου, ἡμεῖς δὲ τοῦ Μωϋσέως ἐσμὲν μαθηταί· <u>29</u>ἡμεῖς οἴδαμεν ὅτι Μωϋσεῖ λελάληκεν ὁ θεός, τοῦτον δὲ οὐκ οἴδαμεν πόθεν ἐστίν.</h4><p><u>30</u>ἀπεκρίθη ὁ ἄνθρωπος καὶ εἶπεν αὐτοῖς·</p><h1>A BLIND BEGGAR </h1><h4>ἐν τούτῳ γὰρ τὸ θαυμαστόν ἐστιν, ὅτι ὑμεῖς οὐκ οἴδατε πόθεν ἐστίν, καὶ ἤνοιξέν μου τοὺς ὀφθαλμούς. <u>31</u>οἴδαμεν ὅτι ἁμαρτωλῶν ὁ θεὸς οὐκ ἀκούει, ἀλλʼ ἐάν τις θεοσεβὴς ᾖ καὶ τὸ θέλημα αὐτοῦ ποιῇ τούτου ἀκούει.<u>32</u>ἐκ τοῦ αἰῶνος οὐκ ἠκούσθη ὅτι ἠνέῳξέν τις ὀφθαλμοὺς τυφλοῦ γεγεννημένου· <u>33</u>εἰ μὴ ἦν οὗτος παρὰ θεοῦ, οὐκ ἠδύνατο ποιεῖν οὐδέν.</h4><p><u>34</u>ἀπεκρίθησαν καὶ εἶπαν αὐτῷ·</p><h1>JEWISH LEADERS </h1><h4>ἐν ἁμαρτίαις σὺ ἐγεννήθης ὅλος καὶ σὺ διδάσκεις ἡμᾶς;</h4><p>καὶ ἐξέβαλον αὐτὸν ἔξω.<br />   <u>35</u>Ἤκουσεν Ἰησοῦς ὅτι ἐξέβαλον αὐτὸν ἔξω καὶ εὑρὼν αὐτὸν εἶπεν ·</p><h1>JESUS </h1><h2>σὺ πιστεύεις εἰς τὸν υἱὸν τοῦ ἀνθρώπου;</h2><p><u>36</u>ἀπεκρίθη ἐκεῖνος καὶ εἶπεν·</p><h1>A BLIND BEGGAR </h1><h4>καὶ τίς ἐστιν, κύριε, ἵνα πιστεύσω εἰς αὐτόν;</h4><p><u>37</u>εἶπεν αὐτῷ ὁ Ἰησοῦς·</p><h1>JESUS </h1><h2>καὶ ἑώρακας αὐτὸν καὶ ὁ λαλῶν μετὰ σοῦ ἐκεῖνός ἐστιν.</h2><p><u>38</u>ὁ δὲ ἔφη·</p><h1>A BLIND BEGGAR </h1><h4>πιστεύω, κύριε·</h4><p>καὶ προσεκύνησεν αὐτῷ.<br />   <u>39</u>Καὶ εἶπεν ὁ Ἰησοῦς·</p><h1>JESUS </h1><h2>εἰς κρίμα ἐγὼ εἰς τὸν κόσμον τοῦτον ἦλθον, ἵνα οἱ μὴ βλέποντες βλέπωσιν καὶ οἱ βλέποντες τυφλοὶ γένωνται.</h2><p><u>40</u>ἤκουσαν ἐκ τῶν Φαρισαίων ταῦτα οἱ μετʼ αὐτοῦ ὄντες καὶ εἶπον αὐτῷ·</p><h1>THE PHARISEES </h1><h4>μὴ καὶ ἡμεῖς τυφλοί ἐσμεν;</h4><p><u>41</u>εἶπεν αὐτοῖς ὁ Ἰησοῦς·</p><h1>JESUS </h1><h2>εἰ τυφλοὶ ἦτε, οὐκ ἂν εἴχετε ἁμαρτίαν· νῦν δὲ λέγετε ὅτι βλέπομεν, ἡ ἁμαρτία ὑμῶν μένει.<br />   <strong>10</strong><u>1</u>Ἀμὴν ἀμὴν λέγω ὑμῖν, ὁ μὴ εἰσερχόμενος διὰ τῆς θύρας εἰς τὴν αὐλὴν τῶν προβάτων ἀλλʼ ἀναβαίνων ἀλλαχόθεν ἐκεῖνος κλέπτης ἐστὶν καὶ λῃστής· <u>2</u>ὁ δὲ εἰσερχόμενος διὰ τῆς θύρας ποιμήν ἐστιν τῶν προβάτων. <u>3</u>τούτῳ ὁ θυρωρὸς ἀνοίγει καὶ τὰ πρόβατα τῆς φωνῆς αὐτοῦ ἀκούει καὶ τὰ ἴδια πρόβατα φωνεῖ κατʼ ὄνομα καὶ ἐξάγει αὐτά. <u>4</u>ὅταν τὰ ἴδια πάντα ἐκβάλῃ, ἔμπροσθεν αὐτῶν πορεύεται καὶ τὰ πρόβατα αὐτῷ ἀκολουθεῖ, ὅτι οἴδασιν τὴν φωνὴν αὐτοῦ· <u>5</u>ἀλλοτρίῳ δὲ οὐ μὴ ἀκολουθήσουσιν, ἀλλὰ φεύξονται ἀπʼ αὐτοῦ, ὅτι οὐκ οἴδασιν τῶν ἀλλοτρίων τὴν φωνήν.</h2><p><u>6</u>Ταύτην τὴν παροιμίαν εἶπεν αὐτοῖς ὁ Ἰησοῦς, ἐκεῖνοι δὲ οὐκ ἔγνωσαν τίνα ἦν ἃ ἐλάλει αὐτοῖς.<br />   <u>7</u>Εἶπεν οὖν πάλιν ὁ Ἰησοῦς·</p><h1>JESUS </h1><h2>ἀμὴν ἀμὴν λέγω ὑμῖν ὅτι ἐγώ εἰμι ἡ θύρα τῶν προβάτων. <u>8</u>πάντες ὅσοι ἦλθον πρὸ ἐμοῦ κλέπται εἰσὶν καὶ λῃσταί, ἀλλʼ οὐκ ἤκουσαν αὐτῶν τὰ πρόβατα. <u>9</u>ἐγώ εἰμι ἡ θύρα· διʼ ἐμοῦ ἐάν τις εἰσέλθῃ σωθήσεται καὶ εἰσελεύσεται καὶ ἐξελεύσεται καὶ νομὴν εὑρήσει. <u>10</u>ὁ κλέπτης οὐκ ἔρχεται εἰ μὴ ἵνα κλέψῃ καὶ θύσῃ καὶ ἀπολέσῃ· ἐγὼ ἦλθον ἵνα ζωὴν ἔχωσιν καὶ περισσὸν ἔχωσιν.<br />   <u>11</u>Ἐγώ εἰμι ὁ ποιμὴν ὁ καλός. ὁ ποιμὴν ὁ καλὸς τὴν ψυχὴν αὐτοῦ τίθησιν ὑπὲρ τῶν προβάτων· <u>12</u>ὁ μισθωτὸς καὶ οὐκ ὢν ποιμήν, οὗ οὐκ ἔστιν τὰ πρόβατα ἴδια, θεωρεῖ τὸν λύκον ἐρχόμενον καὶ ἀφίησιν τὰ πρόβατα καὶ φεύγει — καὶ ὁ λύκος ἁρπάζει αὐτὰ καὶ σκορπίζει — <u>13</u>ὅτι μισθωτός ἐστιν καὶ οὐ μέλει αὐτῷ περὶ τῶν προβάτων.<br />   <u>14</u>Ἐγώ εἰμι ὁ ποιμὴν ὁ καλὸς καὶ γινώσκω τὰ ἐμὰ καὶ γινώσκουσίν με τὰ ἐμά, <u>15</u>καθὼς γινώσκει με ὁ πατὴρ κἀγὼ γινώσκω τὸν πατέρα, καὶ τὴν ψυχήν μου τίθημι ὑπὲρ τῶν προβάτων. <u>16</u>καὶ ἄλλα πρόβατα ἔχω ἃ οὐκ ἔστιν ἐκ τῆς αὐλῆς ταύτης· κἀκεῖνα δεῖ με ἀγαγεῖν καὶ τῆς φωνῆς μου ἀκούσουσιν, καὶ γενήσονται μία ποίμνη, εἷς ποιμήν.<br />   <u>17</u>Διὰ τοῦτό με ὁ πατὴρ ἀγαπᾷ ὅτι ἐγὼ τίθημι τὴν ψυχήν μου, ἵνα πάλιν λάβω αὐτήν. <u>18</u>οὐδεὶς αἴρει αὐτὴν ἀπʼ ἐμοῦ, ἀλλʼ ἐγὼ τίθημι αὐτὴν ἀπʼ ἐμαυτοῦ. ἐξουσίαν ἔχω θεῖναι αὐτήν, καὶ ἐξουσίαν ἔχω πάλιν λαβεῖν αὐτήν· ταύτην τὴν ἐντολὴν ἔλαβον παρὰ τοῦ πατρός μου.</h2><p>   <u>19</u>Σχίσμα πάλιν ἐγένετο ἐν τοῖς Ἰουδαίοις διὰ τοὺς λόγους τούτους. <u>20</u>ἔλεγον δὲ πολλοὶ ἐξ αὐτῶν·</p><h1>THE CROWD </h1><h4>δαιμόνιον ἔχει καὶ μαίνεται· τί αὐτοῦ ἀκούετε;</h4><p><u>21</u>ἄλλοι ἔλεγον·</p><h1>THE CROWD </h1><h4>ταῦτα τὰ ῥήματα οὐκ ἔστιν δαιμονιζομένου· μὴ δαιμόνιον δύναται τυφλῶν ὀφθαλμοὺς ἀνοῖξαι;</h4><p>   <u>22</u>Ἐγένετο τότε τὰ ἐγκαίνια ἐν τοῖς Ἱεροσολύμοις, χειμὼν ἦν,<u>23</u>καὶ περιεπάτει ὁ Ἰησοῦς ἐν τῷ ἱερῷ ἐν τῇ στοᾷ τοῦ Σολομῶνος. <u>24</u>ἐκύκλωσαν οὖν αὐτὸν οἱ Ἰουδαῖοι καὶ ἔλεγον αὐτῷ·</p><h1>JEWISH LEADERS </h1><h4>ἕως πότε τὴν ψυχὴν ἡμῶν αἴρεις; εἰ σὺ εἶ ὁ χριστός, εἰπὲ ἡμῖν παρρησίᾳ.</h4><p><u>25</u>ἀπεκρίθη αὐτοῖς ὁ Ἰησοῦς·</p><h1>JESUS </h1><h2>εἶπον ὑμῖν καὶ οὐ πιστεύετε· τὰ ἔργα ἃ ἐγὼ ποιῶ ἐν τῷ ὀνόματι τοῦ πατρός μου ταῦτα μαρτυρεῖ περὶ ἐμοῦ· <u>26</u>ἀλλʼ ὑμεῖς οὐ πιστεύετε, ὅτι οὐκ ἐστὲ ἐκ τῶν προβάτων τῶν ἐμῶν. <u>27</u>τὰ πρόβατα τὰ ἐμὰ τῆς φωνῆς μου ἀκούουσιν, κἀγὼ γινώσκω αὐτὰ καὶ ἀκολουθοῦσίν μοι,<u>28</u>κἀγὼ δίδωμι αὐτοῖς ζωὴν αἰώνιον καὶ οὐ μὴ ἀπόλωνται εἰς τὸν αἰῶνα καὶ οὐχ ἁρπάσει τις αὐτὰ ἐκ τῆς χειρός μου. <u>29</u>ὁ πατήρ μου ὃ δέδωκέν μοι πάντων μεῖζόν ἐστιν, καὶ οὐδεὶς δύναται ἁρπάζειν ἐκ τῆς χειρὸς τοῦ πατρός. <u>30</u>ἐγὼ καὶ ὁ πατὴρ ἕν ἐσμεν.</h2><p>   <u>31</u>Ἐβάστασαν πάλιν λίθους οἱ Ἰουδαῖοι ἵνα λιθάσωσιν αὐτόν. <u>32</u>ἀπεκρίθη αὐτοῖς ὁ Ἰησοῦς·</p><h1>JESUS </h1><h2>πολλὰ ἔργα καλὰ ἔδειξα ὑμῖν ἐκ τοῦ πατρός· διὰ ποῖον αὐτῶν ἔργον ἐμὲ λιθάζετε;</h2><p><u>33</u>ἀπεκρίθησαν αὐτῷ οἱ Ἰουδαῖοι·</p><h1>JEWISH LEADERS </h1><h4>περὶ καλοῦ ἔργου οὐ λιθάζομέν σε ἀλλὰ περὶ βλασφημίας, καὶ ὅτι σὺ ἄνθρωπος ὢν ποιεῖς σεαυτὸν θεόν.</h4><p><u>34</u>ἀπεκρίθη αὐτοῖς ὁ Ἰησοῦς·</p><h1>JESUS </h1><h2>οὐκ ἔστιν γεγραμμένον ἐν τῷ νόμῳ ὑμῶν ὅτι ἐγὼ εἶπα· θεοί ἐστε;<u>35</u>εἰ ἐκείνους εἶπεν θεοὺς πρὸς οὓς ὁ λόγος τοῦ θεοῦ ἐγένετο, καὶ οὐ δύναται λυθῆναι ἡ γραφή, <u>36</u>ὃν ὁ πατὴρ ἡγίασεν καὶ ἀπέστειλεν εἰς τὸν κόσμον ὑμεῖς λέγετε ὅτι βλασφημεῖς, ὅτι εἶπον· υἱὸς τοῦ θεοῦ εἰμι; <u>37</u>εἰ οὐ ποιῶ τὰ ἔργα τοῦ πατρός μου, μὴ πιστεύετέ μοι· <u>38</u>εἰ δὲ ποιῶ, κἂν ἐμοὶ μὴ πιστεύητε, τοῖς ἔργοις πιστεύετε, ἵνα γνῶτε καὶ γινώσκητε ὅτι ἐν ἐμοὶ ὁ πατὴρ κἀγὼ ἐν τῷ πατρί.</h2><p><u>39</u>Ἐζήτουν οὖν αὐτὸν πάλιν πιάσαι, καὶ ἐξῆλθεν ἐκ τῆς χειρὸς αὐτῶν.<br />   <u>40</u>Καὶ ἀπῆλθεν πάλιν πέραν τοῦ Ἰορδάνου εἰς τὸν τόπον ὅπου ἦν Ἰωάννης τὸ πρῶτον βαπτίζων καὶ ἔμεινεν ἐκεῖ. <u>41</u>καὶ πολλοὶ ἦλθον πρὸς αὐτὸν καὶ ἔλεγον ὅτι</p><h1>THE CROWD </h1><h4>Ἰωάννης μὲν σημεῖον ἐποίησεν οὐδέν, πάντα δὲ ὅσα εἶπεν Ἰωάννης περὶ τούτου ἀληθῆ ἦν.</h4><p><u>42</u>καὶ πολλοὶ ἐπίστευσαν εἰς αὐτὸν ἐκεῖ.</p>","path":"/345","Question1":"What stands out to you in this passage?","Question2":"What truths do you gain about the character of God?","Question3":"What truths do you gain about men and women?","Question4":"What changes are you motivated to make in light of this passage?","prevPath":"/344","nextPath":"/346"}},"pathContext":{}}

